import * as React from 'react';
import { Link } from 'react-router-dom';
import { 
    Card,
    CardContent,
    Button,
    Typography,
    withStyles
} from '@material-ui/core';

import ICard from '../../types/ICard';

interface MyProps {
    item : ICard;
    onClickDelete: any;
    onDragStart: any;
    backgroundColor: {
        backgroundColor: string
    },
    classes: {
        card: string;
        bullet: string;
        title: string;
        pos: string;
        edit: string;
        delete: string;
    }
}

interface MyState {}

interface Istyles {
    card: any;
    bullet: any;
    title: any;
    pos: any;
    edit: any;
    delete: any;
}

const styles : Istyles = {
    card: {
        position: 'relative',
        backgroundColor: 'rgba(255, 255, 255, 0.7)',
        margin: '10px 20px',
        border: '1px solid #000',
        transition: '0.4s',
        boxShadow: '2px 2px 2px 2px rgba(0,0,0,0.25)',
        minWidth: '180px',
        maxWidth: '220px',
        display: 'inline-block',
        backgroundImage: 'url(data:image/png;base64,)',
        '&:hover': {
            boxShadow: '5px 5px 5px 5px rgba(0,0,0,0.5)'
        },
        '& > .actions': {
            pointerEvents: 'none',
            opacity: '0',
            transition: '0.3s'
        },
        '&:hover > .actions': {
            pointerEvents: 'auto',
            opacity: '0.8'
        }
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
    edit: {
        position: 'absolute',
        top: '2px',
        left: '2px',
        minWidth: '32px',
        fontSize: '1rem',
        opacity: '0.7'
    },
    delete: {
        position: 'absolute',
        top: '2px',
        right: '2px',
        minWidth: '32px',
        fontSize: '1rem',
        opacity: '0.7'
    }
}

class MyCard extends React.PureComponent<MyProps, MyState> {

    constructor(props : MyProps) {
        super(props);

    }

    render() : React.ReactNode {
        const { title, description, publishDate } = this.props.item;
        const { classes, onDragStart, item, backgroundColor } = this.props;
        return (
            <Card 
                className={classes.card}
                style={backgroundColor}
                onDragStart={(ev :  React.DragEvent<HTMLDivElement>) : any => onDragStart(ev, item) } 
                draggable={true}
            >
                <CardContent title={description}>
                    <Typography className={classes.title} color="textSecondary" gutterBottom>
                        { publishDate }
                    </Typography>
                    <Typography variant="h5" component="h2">
                        { title }
                    </Typography>
                    <Typography component="p">
                        { description }
                    </Typography>
                </CardContent>
                
                <Button 
                    className={classes.edit + " actions"}
                    variant="contained" 
                    color="primary" 
                    size="small"
                    component={(props:any):any => (<Link to={"/edit/"+this.props.item.id} {...props} />)}
                >&#9881;</Button>

                <Button 
                    className={classes.delete + " actions"}
                    variant="contained" 
                    color="secondary" 
                    size="small"
                    onClick={(ev : React.MouseEvent<HTMLElement>) : void => this.props.onClickDelete(ev, item) }
                >&times;</Button>
                               
            </Card>
        )
    }
}


export default withStyles(styles)(MyCard);
